  @media (max-height: 700px) {
    .canvas-div {
      min-height: 300px !important; } }
  @media (max-width: 820px) {
    .canvas-div {
      width: calc(100% - 10px);
      min-width: 310px !important;
      margin: 5px 5px 15px 5px; } }
  .canvas-div:before {
    border: 1px solid #ddd;
    content: "";
    position: absolute;
    height: 97%;
    width: 99%;
    background-color: #eee;
    right: 25px;
    top: 15px;
    transform: rotate(-1deg);
    z-index: -1;
    -webkit-box-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.1);
    -moz-box-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.1);
    box-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.1); }
  .canvas-div:after {
    border: 1px solid #ddd;
    content: "";
    position: absolute;
    height: 100%;
    width: 99%;
    background-color: #eee;
    top: 5px;
    left: 5px;
    transform: rotate(1deg);
    z-index: -2; }

#custom-dropdown-toolbar {
  z-index: 1000;
  position: relative; }
  #custom-dropdown-toolbar .btn-primary:focus {
    -webkit-box-shadow: none;
    box-shadow: none; }
  #custom-dropdown-toolbar .custom-dropdown-list {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1000;
    padding: 10px;
    margin: 5px 0 0 0;
    float: left;
    min-width: 10rem;
    font-size: 1rem;
    color: #212529;
    text-align: left;
    list-style: none;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid rgba(0, 0, 0, 0.15);
    border-radius: .25rem; }

.character-div {
  text-align: center;
  margin: 0px;
  float: none;
  position: absolute;
  top: 100px;
  left: 100px; }
  .character-div h5 {
    cursor: default;
    margin-bottom: 0px; }
  .character-div .icon {
    margin: auto;
    width: 100%;
    height: 100%;
    cursor: -webkit-grab;
    cursor: allscroll;
    border-radius: 1000px; }
  .character-div .resizable {
    width: 60px;
    height: 60px;
    min-width: 60px;
    min-height: 60px;
    margin: auto;
    padding: 5px;
    resize: horizontal;
    overflow: auto; }

.InitiativeDiv {
  max-width: 70px !important;
  -ms-flex: 0 0 50px;
  flex: 0 0 50px;
  font-size: 3em; }
  @media (max-width: 820px) {
