      max-width: 35px !important;
      padding: 5px !important;
      margin: 0px !important; } }
  .InitiativeDiv .InitiativeButtonDiv {
    cursor: pointer;
    color: #777; }
    .InitiativeDiv .InitiativeButtonDiv:hover {
      color: #000; }

.CharacterDiv {
  position: relative;
  overflow: hidden;
  margin: 5px;
  padding: 0px 5px 0px 5px;
  border-radius: 5px;
  float: left;
  border: 1px solid #000;
  min-width: 160px;
  height: 148px;
  text-align: center; }
  @media (max-width: 820px) {
    .CharacterDiv {
      min-width: 110px; } }
  .CharacterDiv .row {
    margin: 0px -5px 0px -5px; }
  .CharacterDiv .MiddleDiv {
    position: relative;
    height: 126px; }
    .CharacterDiv .MiddleDiv .Name {
      height: 75px;
      overflow: hidden;
      padding: 15px 5px 5px 5px;
      font-size: 1.1em;
      line-height: 1.0em; }
      .CharacterDiv .MiddleDiv .Name div {
        padding: 2px; }
        .CharacterDiv .MiddleDiv .Name div:hover {
          color: #000 !important; }
    .CharacterDiv .MiddleDiv .Select {
      padding: 0px 10px 0px 10px; }
      .CharacterDiv .MiddleDiv .Select select {
