        color: #a0a0a0;
        width: 75px;
        padding-top: 3px; }
        .CharacterDiv .bottom-character-row .remove h5:hover {
          color: red; }

.NewCharacter {
  cursor: pointer;
  float: left;
  height: 148px;
  width: 110px !important;
  min-width: 110px !important;
  text-align: center;
  border: 1px solid #888;
  margin: 5px 5px 5px 5px;
  border-radius: 5px;
  padding-top: 40px;
  color: #888; }
  .NewCharacter:hover {
    color: #000; }



